html,
body,
#root {
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: rgba(1, 0,0, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: absolute;
}

#primary-div{
  position: absolute;
  background-color: black;
}


#exmode{
  position: absolute;
  background-color: black;
}